.square{box-sizing:border-box;border:3px white solid;border-radius:5%;box-shadow:0 5px 15px 0px rgba(0,0,0,0.6);transform:translatey(0px);-webkit-animation:float 3s ease-in-out infinite;animation:float 3s ease-in-out infinite}#primary-sq{height:150px;width:250px;background-color:aquamarine;position:absolute;top:200px;right:200px}@-webkit-keyframes float{0%{box-shadow:0 5px 15px 0px rgba(0,0,0,0.6);transform:translatey(0px)}50%{box-shadow:0 25px 15px 0px rgba(0,0,0,0.2);transform:translatey(-10px)}100%{box-shadow:0 5px 15px 0px rgba(0,0,0,0.6);transform:translatey(0px)}}@keyframes float{0%{box-shadow:0 5px 15px 0px rgba(0,0,0,0.6);transform:translatey(0px)}50%{box-shadow:0 25px 15px 0px rgba(0,0,0,0.2);transform:translatey(-10px)}100%{box-shadow:0 5px 15px 0px rgba(0,0,0,0.6);transform:translatey(0px)}}
